	module.exports = {"data":{"markdownRemark":{"html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Solar Eclipse Summer 2017\"\n        title=\"\"\n        src=\"/static/db13b4307d524578d901acf9c9ce2fcd-55338.jpg\"\n        srcset=\"/static/db13b4307d524578d901acf9c9ce2fcd-a21e6.jpg 163w,\n/static/db13b4307d524578d901acf9c9ce2fcd-4ac53.jpg 325w,\n/static/db13b4307d524578d901acf9c9ce2fcd-55338.jpg 650w,\n/static/db13b4307d524578d901acf9c9ce2fcd-80f1d.jpg 975w,\n/static/db13b4307d524578d901acf9c9ce2fcd-7fc90.jpg 1300w,\n/static/db13b4307d524578d901acf9c9ce2fcd-96c4f.jpg 1950w,\n/static/db13b4307d524578d901acf9c9ce2fcd-dfe85.jpg 3024w\"\n        sizes=\"(max-width: 650px) 100vw, 650px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Hey There! My name is Nicholas F. Rance. I'm 23 years old and I live in New York where I work full-time on my start-up <a href=\"http://www.roundrobin.tech\">RoundRobin</a></p>\n<p>I'm interested in the application of Data Science to build smarter systems.</p>\n<p>The use of Artificial Intelligence to automate tasks</p>\n<p>Biotech to help people live better and longer</p>\n<p>Development Economics to help close the gap between the developed and the developing world</p>\n<p>Blockchain Technology as a way of establish truth in a postmodern 21st Century</p>\n<p>In my spare time I go Scuba Diving and Rock Climbing.</p>\n<p>I'm also an avid League of Legends Esports Fan. #C9Win</p>","frontmatter":{"date":"December 11, 2017","path":"/bio.html","tags":["helloworld"],"title":"About Me"}}},"pathContext":{"prev":{"excerpt":"Link to Podcast:  https://a16z.com/2017/12/14/bio-team-bio-fund-ii/ For my first blog post I'm going to be summarizing a16z's Taking the Pulse on Bio podcast The takeaway \"We are looking for companies where the science risk has been removed or…","html":"<p>Link to Podcast: <a href=\"https://a16z.com/2017/12/14/bio-team-bio-fund-ii/\">https://a16z.com/2017/12/14/bio-team-bio-fund-ii/</a></p>\n<iframe width=\"100%\" height=\"300\" scrolling=\"no\" frameborder=\"no\" src=\"https://w.soundcloud.com/player/?url=https%3A//api.soundcloud.com/tracks/369251810&amp;color=%23ff5500&amp;auto_play=false&amp;hide_related=false&amp;show_comments=true&amp;show_user=true&amp;show_reposts=false&amp;show_teaser=true&amp;visual=true\"></iframe>\n<p>For my first blog post I'm going to be summarizing a16z's Taking the Pulse on Bio podcast</p>\n<h3>The takeaway</h3>\n<p>\"We are looking for companies where the science risk has been removed or greatly derisked\" - <a href=\"twitter.com/VijayPande\">@Vijay</a></p>\n<p>Bio Technology Trajectory</p>\n<p>Impossible -> Possible -> Routine -> Indispensable</p>\n<p>Example given is Genome Sequencing.\na16z believes there are opportunities early in this cycle</p>\n<p>You can split the bio space into two categories of companies: high science risk and low Science risk companies. a16z's biofund is looking for companies where science risk is a small proportion of their overall risk and instead their risk is mainly composed of engineering and scale risks. This archetype of company looks a lot like a startup and that's why a16z thinks it can help these companies most.</p>\n<h3>The rundown</h3>\n<p>There is a crucial shift in the way biological advances will be made and a16z wants to help the companies who change the way bio is done and make profits while doing so.The Shift is from empirical science to an engineering discipline for Bio</p>\n<p>Before we talk about the future of bio let's talk about the way it's being practiced now. </p>\n<ol>\n<li>\n<p>Our understanding of underlying biological mechanisms is limited</p>\n</li>\n<li>\n<p>Developing new therapies is costly wrt time and capital</p>\n</li>\n<li>\n<p>‎Techniques used in one domain of bio are not easily transferred to others.</p>\n</li>\n</ol>\n<h5>The solution</h5>\n<p>Apply engineering techniques to bio specifically from the domains of ML/AI</p>\n<ol>\n<li>\n<p>ML/AI techniques can function in poorly understood systems and still produce new knowledge and actionable insights. Freenome uses AI to understand the relationship between the poorly understood immune signals in the blood and certain cancers allowing people to get early treatment</p>\n</li>\n<li>\n<p>For illnesses where the cause is well understood (example given sickle cell anemia) then engineering approaches using tools such as CRISPR and Gene Therapy can accelerate the speed of development of new therapies.</p>\n</li>\n<li>\n<p>Plug and Play nature of CRISPR and modern therapies compared to bespoke drug, you just alter the genetic code that you write</p>\n</li>\n</ol>\n<h5>On The FDA</h5>\n<p>The partners found that FDA was helpful legislatively in allowing innovations in healthspace. E.g. first gene therapy approval and first digital health treatment approval.\n\"Regulatory risk is a euphemism for scientific risk.\"\nGene therapy was believed to be incredibly risky but was shown to be very effective in treating a form of inheritable blindness so that the FDA panel approved the treatment unanimously - a 13-0 vote</p>\n<h5>On Network Effects</h5>\n<p>PatientPing - Reduces patient risk. Let's HC providers know when one of their patients get treatment at any other provider. The more subscribers and providers they have the more granularly you can track your patients</p>\n<p>Network Effects make companies more defendable. Data network effects are newer than parents. Freenome or Cardiogram get more data with more customers then their tests get more accurate then they get a better product which gets them more customers</p>\n<p>The patent window is shrinking and the data network effect never goes off patent</p>\n<h3>Opportunities</h3>\n<p><strong>Application of AI techniques</strong>\n*Dermatology\n*Retinal screening\n*Facial analysis for genetic diseases\n*Analysing Pulse from an Apple watch to diagnose heart conditions</p>\n<p><strong>Non AI techniques</strong>\n*Crispr\n*Gene Therapy\n*Cell Therapy\n*Gene Sequencing price and duration reduction</p>\n<h4>Testing/Trial Opportunities</h4>\n<p>Phase 3 Drug Trials, after much time and capital are invested, is where a lot of new therapies fail. There are developments to make testing of therapies more efficient before you get to human trials as well as after.</p>\n<p>The problem is that cell tests in labs and animal models (the preclinical phase) are poor predictors of human success. Animals can be replaced by \"organs on chips or humans on chips\" that allow for better prediction of being beneficial to humans.</p>\n<p>There will also be less volunteers needed for human trials. With more targeted drugs you need more specific patients which means higher signal to noise ratio. Recruiting for trials is also made easier. Facebook partnered with the Michael J Fox foundation to pilot FB as a tool to recruit patients for a Parkinson's disease trial, this lead to a 96% cost + time reduction in finding trial patients.\n(I couldn't find a reference to this partnership in the news)</p>\n<h4>Digital health Opportunities</h4>\n<p>Vijay: The opportunity is that you can design therapeutics that have no toxicity. Behavioural therapy is very expensive and hard to scale e.g. The CDC's diabetes prevention program. Omata can take these established therapies and engineer + scale it + A/B test it at scale. A/B testing is the tech equivalent of clinical trials. The challenge with this opportunity is finding the areas where you can make a similar impact apart from Type 2 diabetes</p>\n<h3>My notes</h3>\n<p>Vijay really emphasizes having the domain expertise in biology and computer science. <a href=\"twitter.com/gabeotte\">Gabe</a> of <a href=\"freenome.com\">Freenome</a> worked at Apple as a SE in addition to studying molecular biology</p>","id":"/home/nick-dev/workspace/projects/gatsby-blog-starter-kit/src/pages/12-22-2017-podcast-rundown-a16z-biofund-ii/index.md absPath of file >>> MarkdownRemark","timeToRead":4,"frontmatter":{"date":"2017-12-22T13:35:13.234Z","path":"/podcast-rundown-a16z-biofund-ii.html","tags":["bio"],"title":"Podcast Rundown: a16z on it's second bio fund"}},"next":false}}

